import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import GlobalTabel from "../GlobalComponent/GlobalTabel";

class AccordListBarangSet extends Component {
  constructor(props) {
    super(props);
    this.state = {
      detail: [],
      columns: [
        {
          dataField: "kode_barcode_set",
          text: "Kode Barcode",
        },
        {
          dataField: "total_bruto",
          text: "Total Bruto",
        },
        {
          dataField: "total_netto",
          text: "Total Netto",
        },
        {
          dataField: "status_active",
          text: "Status",
          align: "center",
          formatter: (rowContent, row) => {
            if (row.status_active === true) {
              return (
                <div className="col-lg-12">
                  <div className="row">
                    <div className="col-lg-3"></div>
                    <div className="badge-pill badge-success col-lg-6 text-center">
                      AVAILABLE
                    </div>
                  </div>
                </div>
              );
            } else
              return (
                <div className="col-lg-12">
                  <div className="row">
                    <div className="col-lg-3"></div>
                    <div className="badge-pill badge-danger col-lg-6 text-center">
                      NOT-AVAILABLE
                    </div>
                  </div>
                </div>
              );
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="col-lg-12">
                <div className="row">
                  <div className="col-lg-6 text-center">
                    <button
                      className="btn btn-warning mr-2"
                      data-tut="reactour_3"
                      onClick={() => this.print(row)}
                    >
                      Print
                    </button>
                  </div>
                  <div className="col-lg-6 text-center">
                    <button
                      className="btn btn-outline-primary"
                      data-tut="reactour_3"
                      data-toggle="modal"
                      data-target="#detail_barangSet"
                      onClick={() =>
                        this.setState({
                          detail: row.detail_barang,
                        })
                      }
                    >
                      Detail
                    </button>
                  </div>
                </div>
              </div>
            );
          },
        },
      ],
      columns2: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar\nCetak(%)",
        },
        // {
        //   dataField: "plastik",
        //   text: "P",
        // },
        // {
        //   dataField: "kulit",
        //   text: "K",
        // },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
        },
        {
          dataField: "kadar_jual",
          text: "Harga\n(%)",
        },
        {
          dataField: "netto",
          text: "Murni\n(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc_persen",
          text: "%",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc",
          text: "Disc\n(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "netto_disc",
          text: "Netto\nDisc(Gr)",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos\n(Rp)",
        },
      ],
    };
  }

  print(data) {
    let feedback = "";
    feedback += `<xpml><page quantity='0' pitch='35.0 mm'></xpml>I8,A\n`;
    feedback += `ZN\n`;
    feedback += `q504\n`;
    feedback += `O\n`;
    feedback += `JF\n`;
    feedback += `KIZZQ0\n`;
    feedback += `KI9+0.0\n`;
    feedback += `ZT\n`;
    feedback += `Q280,B25\n`;
    feedback += `Arglabel 350 31\n`;
    feedback += `exit\n`;
    feedback += `KI80\n`;
    feedback += `<xpml></page></xpml><xpml><page quantity='1' pitch='35.0 mm'></xpml>N\n`;
    feedback += `A400,275,2,2,1,2,N,"BARCODE SET"\n`;
    // BARCODE
    feedback += `B350,238,2,1,1,10,73,N,"${data.kode_barcode_set}"\n`;
    // BARCODE
    feedback += `A400,155,2,2,1,2,N,"${data.kode_barcode_set}"\n`;
    feedback += `A400,107,2,2,1,2,N,"Bruto"\n`;
    feedback += `A400,62,2,2,1,2,N,"Netto"\n`;
    feedback += `A325,107,2,2,1,2,N,"${parseFloat(data.total_bruto).toFixed(
      3
    )}"\n`;
    feedback += `A325,62,2,2,1,2,N,"${parseFloat(data.total_netto).toFixed(
      3
    )}"\n`;
    feedback += `P1\n`;
    feedback += `<xpml></page></xpml><xpml><end/></xpml>`;
    document.getElementById("myInput").value = feedback;
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("myInput").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_barcode.txt";
    document.body.appendChild(element);
    element.click();
  }

  render() {
    return (
      <div className="col-lg-12 mt-2">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-primary">
            <div className="card-header" id="headingOne">
              <div className="row">
                <div className="col-lg-12">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapseBarangSet"
                      aria-expanded="true"
                      aria-controls="collapseBarangSet"
                    >
                      List Barang Set
                    </a>
                  </h5>
                </div>
              </div>
            </div>
            <div
              id="collapseBarangSet"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <form onSubmit={this.props.handleSubmit}>
                  <div className="row">
                    <div className="d-none">
                      <textarea id="myInput"></textarea>
                    </div>
                    <div className="col-lg-12">
                      <GlobalTabel
                        keyField="kodeJenis"
                        data={this.props.BarangSet || []}
                        columns={this.state.columns}
                        textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                      />
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <div
          id="detail_barangSet"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Detail Barang Set
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <div className="col-lg-12">
                  <GlobalTabel
                    keyField="kodeJenis"
                    data={this.state.detail || []}
                    columns={this.state.columns2}
                    textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                  />
                </div>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AccordListBarangSet = reduxForm({
  form: "AccordListBarangSet",
  enableReinitialize: true,
})(AccordListBarangSet);
export default connect((state) => {
  return {
    BarangSet: state.provinsi.BarangSet,
  };
})(AccordListBarangSet);
